import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

import { Loader, SelectField, TextField } from '../components';

import { UserCreate } from '../api';
import { useSnackbar } from '../hooks';
import { CreateUser, UserRole, UserRoles } from '../utils/dataTypes';

const validationSchema = yup.object({
  fullName: yup.string().required('Full name is required'),
  phone: yup.string(),
  userRole: yup.string().oneOf(UserRoles).required('User role is required'),
  jobTitle: yup.string().required('Job title is required'),
  email: yup
    .string()
    .email('Enter a valid email')
    .required('Email is required'),
  password: yup
    .string()
    .min(8, 'Password should be of minimum 8 characters length')
    .required('Password is required'),
});

const initialValues = {
  fullName: '',
  phone: undefined,
  userRole: 'Admin' as UserRole,
  jobTitle: '',
  email: '',
  password: '',
};

export default function CreateUserForm() {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const { mutate, isLoading } = useMutation(UserCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['users']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateUser>({
    initialValues,
    enableReinitialize: true,
    validationSchema: validationSchema,
    onSubmit: (values) => {
      mutate(values as unknown as CreateUser);
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box sx={{ p: 3, display: 'grid' }}>
          <Box key="controls" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, width: '50%' }}>
              <TextField
                fieldName="fullName"
                label="Full Name"
                formik={formik}
              />
              <SelectField
                fieldName="userRole"
                label="User Role"
                items={UserRoles}
                formik={formik}
              />
              <TextField
                fieldName="jobTitle"
                label="Job Title"
                formik={formik}
              />
            </Box>
            <Box key="right" sx={{ p: 1, width: '50%' }}>
              <TextField fieldName="phone" label="Phone" formik={formik} />
              <TextField fieldName="email" label="Email" formik={formik} />
              <TextField
                fieldName="password"
                label="Password"
                formik={formik}
              />
            </Box>
          </Box>
          <Box key="buttons" sx={{ display: 'inline-flex' }}>
            <Box key="left" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="submit"
              >
                Create
              </Button>
            </Box>
            <Box key="right" sx={{ p: 1, paddingTop: 0, width: '50%' }}>
              <Button
                fullWidth
                variant="contained"
                size="small"
                color="primary"
                type="button"
                onClick={handleClose}
              >
                Cancel
              </Button>
            </Box>
          </Box>
        </Box>
      </form>
      <Loader visible={isLoading} />
    </Box>
  );
}
